<!---->
<template>
  <div >
    <el-form ref="ruleForm" :model="ruleForm" :rules="rules"  style="width: 80%;" label-width="100px">
      <!--      -->
      <el-form-item label="" prop="title" >
        <el-input placeholder="" v-model="ruleForm.title" class="input-with-select">
          <el-select v-model="flag" slot="prepend" placeholder="" style="width: 100px">
            <el-option label="" value="true"></el-option>
            <el-option label="" value="false"></el-option>
          </el-select>
        </el-input>
      </el-form-item>

      <el-row>
        <el-col :span="12"><div class="grid-content bg-purple">
          <el-form-item label="" prop="firstPicture">
            <!--el-selectclearable filterable            -->
            <el-select   v-model="tagNames"  clearable multiple filterable allow-create default-first-option placeholder="">
              <el-option v-for="item in tags" :value="item.id"   :label="item.name"></el-option>
            </el-select>
          </el-form-item>
        </div></el-col>
        <el-col :span="12"><div class="grid-content bg-purple-light">
          <el-form-item label="" prop="firstPicture">
            <el-select   v-model="types" clearable  filterable allow-create default-first-option placeholder="">
              <el-option v-for="item in type" :value="item.id"   :label="item.name"></el-option>
            </el-select>
          </el-form-item>
        </div></el-col>
      </el-row>
      <!--      -->
      <el-form-item  prop="content" >
        <mavonEditor v-model="ruleForm.content"/>
      </el-form-item>
      <!--     -->
      <el-form-item label="" prop="description"  >
        <el-input v-model="ruleForm.description" maxlength="100" placeholder="" show-word-limit type="textarea"/>
      </el-form-item>
      <!--     -->
      <el-form-item label="" prop="statementMessage"  >
        <el-input v-model="ruleForm.statementMessage" maxlength="100" placeholder="" show-word-limit type="textarea"/>
      </el-form-item>
      <!--      -->
      <el-form-item label="" prop="imageUrl">
        <div style="height: 120px;width: 200px;border: 1px solid">
        <!--          -->
          <el-image
              style="width: 200px; height: 120px"
              :src="ruleForm.imageUrl"
              :preview-src-list="[ruleForm.imageUrl]">
          </el-image>
        </div>
        <el-button type="primary" @click="open"></el-button>
      </el-form-item>
      <!--      -->
      <el-form-item >
        <el-row>
          <el-col :span="6"><div class="grid-content bg-purple">
            <el-checkbox v-model="ruleForm.appreciation" label=""></el-checkbox>
          </div></el-col>
          <el-col :span="6"><div class="grid-content bg-purple-light">
            <el-checkbox v-model="ruleForm.commentabled" label=""></el-checkbox>
          </div></el-col>
          <el-col :span="6"><div class="grid-content bg-purple">
            <el-checkbox v-model="ruleForm.shareStatement" label=""></el-checkbox>
          </div></el-col>
          <el-col :span="6"><div class="grid-content bg-purple-light">
            <el-checkbox v-model="ruleForm.recommend" label=""></el-checkbox>
          </div></el-col>
        </el-row>
      </el-form-item>
      <!--      -->
      <el-form-item>
        <el-button @click="goOut"></el-button>
        <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
        <el-button type="primary" @click="insert('ruleForm')"></el-button>
      </el-form-item>

    </el-form>

    <!--   -->
    <el-dialog
        title=""
        :visible.sync="dialogVisible"
        append-to-body
        width="50%"
        :before-close="handleClose">
      <!---->
      <el-tabs v-model="activeName" >
        <!---->
        <el-tab-pane label="" name="first">
          <!--uploadBtn  favoritesDeletionBtn-->
          <ImgFunctionIndex :uploadBtn="false" :favoritesDeletionBtn="false" @imgData="getImgData"/>
        </el-tab-pane>
        <!---->
        <el-tab-pane label="" name="second">
          <el-upload
              class="upload-demo"
              drag
              action="http://localhost:9090/image/ossUpload"
              :on-success="imgUpload"
              multiple>
            <i class="el-icon-upload"></i>
            <div class="el-upload__text"><em></em></div>
            <div class="el-upload__tip" slot="tip">jpg/png500kb</div>
          </el-upload>
        </el-tab-pane>
      </el-tabs>
    </el-dialog>
  </div>
</template>
<script>
import { mavonEditor } from 'mavon-editor'
import "mavon-editor/dist/css/index.css";
import router from "../../router";
import {selectAllType} from "../../api/type";
import {selectAllTag} from "../../api/tag";
import request from "../../utils/request";
//
import ImgFunctionIndex from "../imgFunction"
export default  {
  name: "releaseBlogIndex",
  components: {
    mavonEditor,
    ImgFunctionIndex
  },
  data() {
    return {
      dialogVisible: false,//
      activeName: 'second',//
      // url:'',
      ruleForm: {
        appreciation:true,//
        commentNumber:'',//
        commentabled:true,//
        content:'',//
        createTime:'',//
        description:'',//
        flag: '',//
        id:'',//id
        imageId:'',//id
        imageUrl:"",//URL
        published:true,//
        recommend:false,//
        shareStatement:true,//
        statementMessage:'',//
        title: '',//
        typeId: '',//id
        typeName:'',//
        updateTime:'',//
        views: '',//

        tagList:'',//id
        tagName:'',//

      },
      type:'',//
      types:'',

      tags:[],//
      tagIds:'',//idid
      tagNames:'',//id
      flag:'',//
      uploadPath:'',//
      rules: {
        title: [{required: true, message: '', trigger: 'blur',},],
        imageUrl:[{required: true, message: '', trigger: 'blur',},],
        description:[{required: true, message: '', trigger: 'blur',},],
      },
    }
  },
  created() {
    this.loadType()//
    this.loadTag()//
    this.logBlog()//
  },
  methods: {
    //
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.ruleForm.published=false//
          //
          this.setUp()
          const _this=this
          //1
          setTimeout(function (){
            //id
            if(_this.ruleForm.id!==""){
              //
              _this.updateBlog()
            }else {
              //
              _this.add();
            }
            //
            sessionStorage.removeItem("blog")
            //
            router.push({path:'/layout/blogs'})
          },1000)

        } else {
          return false
        }
      })
    },
    //
    insert(formName){
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.ruleForm.published=true//
          //
          this.setUp()
          const _this=this
          //1
          setTimeout(function (){
            //id
            if(_this.ruleForm.id!==""){
              //
              _this.updateBlog()
            }else {
              //
              _this.add();
            }
            //
            sessionStorage.removeItem("blog")
            //
            router.push({path:'/layout/blogs'})
          },1000)
        } else {
          return false
        }
      })
    },
    //
    setUp(){
      //
      this.ruleForm.typeId=this.types
      //
      //
      if(!isNaN(this.tagNames.join(",")[0])){
        this.ruleForm.tagList=this.tagNames.join(",")
      }else {
        request.get("/tag/getTagByNames/"+this.tagNames.join(",")).then(res=>{
          //id
          this.ruleForm.tagList=res.data.data.ids
        })
      }
      //
      if(this.flag===""){
        this.ruleForm.flag=true
      }else if(this.flag==="true"){
        this.ruleForm.flag=true
      }else {
        this.ruleForm.flag=false
      }
    },
    //
    updateBlog(){
      //
      request.patch("/blog/editBlog",this.ruleForm).then(res=>{
        if(res.data.result){
          this.$message.success("")
        }else {
          this.$message.error("")
        }
      })
    },
    //
    getImgData(val){
      this.ruleForm.imageId=val.id
      this.ruleForm.imageUrl=val.imgUrl
      //
      this.dialogVisible=false
    },
    //
    open(){
      //
      this.dialogVisible=true
    },
    //
    imgUpload(response,file,fileList){
      if(response.result){
        //
        this.ruleForm.imageId=response.data.image.id
        this.ruleForm.imageUrl=response.data.image.imgUrl
        this.$message.success("")
        //
        this.dialogVisible=false
      }else {
        //
        this.$message.error("")
      }
    },
    //
    handleClose(done) {
      this.$confirm('')
          .then(_ => {
            done();
          })
          .catch(_ => {});
    },
    //
    loadType(){
      selectAllType().then(res=>{
        this.type=res.data.data.types
      })
    },
    //
    loadTag(){
      selectAllTag().then(res=>{
        this.tags=res.data.data.tagList
      })
    },
    //
    add(){
      request.post("/blog/addBlog",this.ruleForm).then(res=>{
        if(res.data.result){
          this.$message.success("")
        }else {
          this.$message.error("")
        }
      })
    },
    //
    goOut(){
      sessionStorage.removeItem("blog")
      router.push({path:'/layout/blogs'})
    },
    //
    logBlog(){
      if(sessionStorage.getItem("blog")!=null){
        //
        this.ruleForm=JSON.parse(sessionStorage.getItem("blog"))
        //id
        this.tagIds=JSON.parse(sessionStorage.getItem("tagIds")).tagIds.split(",")
        //
        this.tagNames=JSON.parse(sessionStorage.getItem("tagIds")).tagNames.split(",")
        //
        this.flag=this.ruleForm.flag==true? this.ruleForm.flag="":this.ruleForm.flag=""
        //
        this.types=this.ruleForm.typeId
      }
    },
  },
}
</script>

<style scoped>
.mavonEditor {
  width: 100%;
  height: 100%;
}

</style>