<!---->
<template>
  <div class="">
    <!---->
    <div class="btn">
      <el-radio-group v-model="status" size="small" @change="changeStatus">
        <el-radio-button :label="false"></el-radio-button>
        <el-radio-button :label="true"></el-radio-button>
      </el-radio-group>
      <!---->
      <el-button size="small" type="success" @click="dialogTableVisible=true" v-if="uploadBtn"></el-button>
    </div>

    <!---->
    <el-row :gutter="10">
      <el-col :xs="12" :sm="6" :md="6" :lg="4" v-for="(img,index) in images" :key="index"  class="image-item">
        <el-image
            :lazy="true"
            fit="cover"
            placeholder=""
            style="height: 120px"
            :src="img.imgUrl"
            :preview-src-list="[img.imgUrl]">
          <div slot="error" class="image-slot">
            <i class="el-icon-picture-outline"></i>
          </div>
        </el-image>
        <div class="image-action" >
          <el-button type="warning" :icon="img.imgFlag?'el-icon-star-on':'el-icon-star-off'" circle size="small" @click="onCollect(img)" v-if="favoritesDeletionBtn"></el-button>
          <!--class  {css} true false -->
          <!--            <i :class="{'el-icon-star-on':img.flag,'el-icon-star-off':!img.flag}" @click="onCollect(img.flag)"></i>-->
          <!--            <i class="el-icon-delete-solid"></i>-->
          <el-button type="danger" icon="el-icon-delete" circle size="small" @click="deleteImag(img)" v-if="favoritesDeletionBtn"></el-button>
          <el-button type="success" icon="el-icon-check" circle size="small" v-if="!favoritesDeletionBtn" @click="selectBtn(img)"></el-button>
        </div>
      </el-col>
    </el-row>

    <!---->
    <el-dialog title="" :visible.sync="dialogTableVisible" :append-to-body="true" width="400px">
      <el-upload
          v-if="dialogTableVisible"
          class="upload-demo"
          drag
          action="http://localhost:9090/image/ossUpload"
          :on-success="imgUpload"
          multiple>
        <i class="el-icon-upload"></i>
        <div class="el-upload__text"><em></em></div>
        <div class="el-upload__tip" slot="tip">jpg/png500kb</div>
      </el-upload>
    </el-dialog>

    <!---->
    <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        background
        :current-page.sync="currentPage"
        :page-sizes="[12, 16, 20, 24]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
    </el-pagination>
  </div>
</template>

<script>
import request from "../../utils/request";

export default {
  name: "ImgFunctionIndex",
  components: {},
  props:{
    //
    uploadBtn: {
      type: Boolean,
      default: true //
    },
    //
    favoritesDeletionBtn: {
      type: Boolean,
      default: true //
    }
  },
  data() {
    return {
      currentPage: 1,//
      pageSize:12,//
      total: 0,//
      dialogTableVisible:false,//
      status:false,//
      flag:'',//
      images:[
        {
          id:'',//id
          imgFlag:'',//
          imgUrl:'',//url
        }
      ],//
      imgData:{},
    }
  },
//
  created() {
    //
    this.loadImagePage()
  },
//DOM
  mounted() {
  },
//
  methods: {
    //
    handleSizeChange(pageSize) {
      this.pageSize=pageSize
      //
      this.loadImagePage()
    },
    //
    handleCurrentChange(pageNum) {
      this.currentPage=pageNum
      //
      this.loadImagePage()
    },
    //
    loadImagePage(){
      request.get("/image/getTypePage",{params:{
          pageNum:this.currentPage,//
          pageSize:this.pageSize,//
          search:this.flag,//
        }}).then(res=>{
        //
        this.total=res.data.data.imagePage.total
        //
        this.images=res.data.data.imagePage.results
      })
    },
    //
    changeStatus(value){
      if(value){
        this.flag=true
      }else {
        this.flag=''
      }
      this.loadImagePage()
    },
    //()
    onCollect(img){
      request.patch("/image/updateImgFlagById",img).then(res=>{
        if(res.data.result){
          this.$message.success(res.data.message)
        }else {
          this.$message.error(res.data.message)
        }
        //
        this.loadImagePage();
      })
    },
    //
    deleteImag(img){
      request.delete("/image/deleteImgById/"+img.id).then(res=>{
        if(res.data.result){
          this.$message.success(res.data.message)
        }else {
          this.$message.error(res.data.message)
        }
        //
        this.loadImagePage();
      })
    },
    //
    imgUpload(response,file,fileList){
      //
      this.dialogTableVisible=false
      //
      this.loadImagePage()
    },
    //
    selectBtn(img){
      this.imgData=img
      this.$emit("imgData",this.imgData)
    },
  },
}
</script>

<style scoped>
.btn{
  margin: 10px;
  display: flex;
  justify-content: space-between;
}
.image-item{
  position: relative;
}
.image-action{
  font-size: 20px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  color: #fff;
  height: 40px;
  background-color: rgba(0,0,0,0.5);
  position: absolute;
  bottom: 4px;
  left: 5px;
  right: 5px;
  transition: 0.5s;
  opacity: 0;
}
.image-item:hover .image-action{
  opacity: 1;
}
/**/
.el-image{
  width: 100%;
}
</style>
        
